/*

  Responsive 

*/

@media only screen and (max-width: 980px) {
  .font-20.font-18,.font-16{
    font-size:16px;
  }
  .font-30,.font-24{
    font-size:20px;
  }
  .font-50,.font-40{
    font-size:30px;
  }
  .line-40,.line-35{
    line-height:30px;
  }
  .line-50{
    line-height:40px;
  }
}
.rf-column {
    width:24% !important;
  }


@media screen and (max-width: 1020px){
    .rf-column {
    width:30% !important;
  }
 
    
}
@media only screen and (max-width: 780px) {
  .rf-column {
    width:47% !important;
  }
  
}

@media only screen and (max-width: 450px) {
  .rf-column {
    width:100% ;
  }

}
